
.field-input{
    width: 180px;
    font-size: 16px;
    height: auto;
    padding: 5px 10px ;
    border-radius: 2px;
    border: 1px solid;
}
.blank-input {
    width: 50px;
    font-size: 16px;
    height: auto;
    padding: 5px 10px;
    border-radius: 2px;
    border: 1px solid;
}
.close-button {
    border: none;
    padding: 1px  1px;
    background-color: white;
    margin: 1px 1px 1px 1px;
}
.close-button:hover {
    background-color: #EDF1F3;
}
.chooseTypeButton {
    display: flex;
    height: auto;
    width: 180px;
    padding: 5px 10px ;
    border-radius: 2px;
    border: 1px solid;
    font-size: 16px;
    justify-content: space-between;
}

.dropdown-arrow {
    margin-left: 8px;
    font-size: 10px;
    transition: transform 0.3s ease;
}
.dataFormat-select {
    font-size: 16px;
}
.dataFormat-input {
    font-size: 16px;
}
.dataFormat-checkbox {
    width: 22px;
    height: 22px;
}
.dataFormat-label {
    display: flex;
    align-items: center;
    line-height: normal;
    font-size: 16px;
}

