.title-container {
    display: flex;
    gap: 170px
}
.big-button {
    background-color: #634F7D;
    font-size: 16px;
    color: #f0f0f0;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
}

.container {
    display: flex; /* Use flexbox to make children elements align in a row */
    align-items: center; /* Align items vertically in the center */
    margin-bottom: 10px; /* Add some margin between each field container */
    gap: 10px;
    margin-top: 10px;
}
.big-button:disabled {
    background-color: #ccc;
}

.spinner {
    display: inline-block;
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-top-color: green;
    /* Blue spinner */
    border-radius: 50%;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -10px;
    /* Offset by half the height */
    margin-left: -10px;
    /* Offset by half the width */
    animation: spin 1s linear infinite;
}

.spinner[hidden] {
    display: none;
}
@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}
